.tell-a-tale,
.vignette1 {
  position: relative;
}
.tell-a-tale {
  font-size: 24px;
  font-weight: 600;
}
.frame {
  width: 250px;
  height: 40px;
  overflow: hidden;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  gap: 16px;
}
.motivation-is-the {
  position: relative;
  display: inline-block;
  width: 1144px;
  height: 50px;
  flex-shrink: 0;
}
.mask-group-icon,
.tellatale-child {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.mask-group-icon {
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.tellatale-child {
  border-radius: 50%;
  background: linear-gradient(
    180deg,
    rgba(0, 116, 153, 0),
    rgba(27, 92, 113, 0.64)
  );
}
.tellatale-item {
  position: absolute;
  height: 18.18%;
  width: 79.55%;
  top: 40.91%;
  right: 10.45%;
  bottom: 40.91%;
  left: 10%;
  border-radius: var(--br-xl);
  background-color: var(--color-gray);
}
.tell-a-tale1 {
  position: absolute;
  top: 45.45%;
  left: 33.18%;
  font-weight: 600;
}
.tellatale {
  position: relative;
  width: 220px;
  height: 220px;
}
.frame1,
.tellatale-parent,
.vignette {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.tellatale-parent {
  flex-direction: row;
  flex-wrap: wrap;
  gap: 20px;
  font-size: var(--font-size-base);
  color: var(--color-white);
}
.frame1,
.vignette {
  overflow: hidden;
  flex-direction: column;
  text-align: left;
  font-family: var(--font-roboto);
}
.frame1 {
  width: 1180px;
  height: 293px;
  flex-shrink: 0;
  gap: 23px;
  font-size: 13px;
  color: #3f3f3f;
}
.vignette {
  position: relative;
  background-color: #f6f5e6;
  width: 100%;
  height: 600px;
  padding: 81px 130px;
  box-sizing: border-box;
  gap: 40px;
  font-size: 34px;
  color: var(--color-steelblue);
}
